#container {
    background-color: rgb(12, 12, 35);
    color: rgb(121, 128, 171);
    position: relative;
    bottom: 0;
    height: 78px;
    text-align: center;
    width: 100%;
    z-index: 1;

    a {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin: 6px 0;
        text-decoration: none;
        color: rgb(121, 128, 171);

        p {
            margin: 0 12px 0 0;
            position: relative;
            top: 12px;
        }
    }

    p {
        margin: 0 0 6px 0;
        color: rgb(121, 128, 171)
    }
}