@use "global.module.scss" as *;

.container {
    display: flex;
    flex-direction: column;
    position: relative;
    margin: 15px 0 auto 0;
    width: 210px;
    min-width: 210px;
    border-radius: 15px;
    border: 1px solid #ccc;
    box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .18);
    overflow: hidden;
}

.navButton {
    background-color: white;
    margin: 0;
    padding: 0;
    height: 100px;
    display: flex;
    flex-direction: row;
    border: none;
    border-bottom: 1px solid #ddd;
    cursor: pointer;

    &:hover {
        background-color: #f2f2f2;
        transition: all 0.2s ease-in-out;

        .buttonArrowContainer {
            background-color: #f2f2f2;
            transition: all 0.2s ease-in-out;
        }
    }
}

.buttonInput {
    position: absolute;
    pointer-events: none;
    opacity: 0;
    z-index: -1;
    
    &:checked {
        + .navButton {
            background-color: $primaryColor;

            .buttonName {
                color: white;
            }
            
            .buttonArrowContainer {
                background-color: #035876;
            }

            .buttonArrow {
                color: white;
            }
        }
    }
}

.buttonTabColor {
    margin: 0;
    min-width: 6px;
}

.buttonText {
    margin: 0;
    padding: 9px;
    pointer-events: none;
}

.buttonCode {
    margin: 0;
    color: #999;
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 20px;
}

.buttonName {
    margin: 6px 0 0 0;
    padding: 0;
    font-size: 12pt;
    pointer-events: none;
}

.buttonArrowContainer {
    background-color: #f7f7f7;
    margin: 0 0 0 auto;
    max-width: 30px;
    min-width: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    pointer-events: none;
}

.buttonArrow {
    color: #007097;
    padding: 6px;
}

.buttonStar {
    padding: 0 0 0 6px;
    color: $yellow;
}