@use "global.module.scss" as *;

#wrapper {
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#container {
    margin: 0;
    color: black;
    background-color: white;
    font-size: 15px;
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    flex-direction: column;
    transform-origin: top left;
    border: 1px solid #ddd;
    border-radius: 12px;
    box-shadow: 0 0 6px 0 rgba(0, 0, 0, .2);
    overflow: hidden;
    scroll-behavior: none;
    z-index: 1;
}

#mainContent {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
}

#topContainer {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
}

#vizContainer {
    margin: 0;
    padding: 0;
    position: relative;
    z-index: 0;
    overflow: hidden;
}

#tableauBlocker {
    background-color: white;
    margin: 0 0 -25px 0;
    position: relative;
    bottom: 25px;
    height: 25px;
    width: 100%;
    z-index: 2;
    border: none;
}

#isolatedTableauVizContainer {
    position: absolute;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    margin: 0;
    opacity: 1;
    pointer-events: none;
}

#sidebar {
    background-color: white;
    min-width: 210px;
    margin: 0 3px 0 0;
    padding: 0 0 0 12px;
    z-index: 1;
}

#sidebar-yellow {
    background-color: $primaryColor;
    min-width: 210px;
    margin: 0 3px 0 12px;
    z-index: 1;
}