 #container {
     background-color: #f7f7f7;
     margin: 12px 0 0 0;
     display: flex;
     flex-direction: column;
     padding: 12px;
     border-radius: 12px;
     box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .18);
 }

 .shelf {
     background-color: white;
     display: flex;
     flex-direction: row;
     justify-content: space-between;
     align-items: center;
     height: 60px;
     padding: 12px;
     border-radius: 12px;
     box-sizing: border-box;
     box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .18);
 }

 .hasTooltip {
     position: relative;
 }

 .icon {
     position: relative;
     font-size: 30pt;
     cursor: pointer;
     transition: all 0.2s ease-in-out;

     &:hover {
         translate: 0 -2px;
         transition: all 0.2s ease-in-out;
     }
 }

 #faFilePdf {
     color: #973126;

     &:hover {
         color: #007097;
     }
 }

 #faFileExcel {
     color: #0a9544;

     &:hover {
         color: #007097;
     }
 }

 #faFilePowerpoint {
     color: #e74c3c;

     &:hover {
         color: #007097;
     }
 }

 .tool {
     color: #ccc;
     position: relative;
     font-size: 15pt;
     cursor: pointer;
     transition: all 0.2s ease-in-out;

     &:hover {
         translate: 0 -2px;
         color: #007097;
         transition: all 0.2s ease-in-out;
     }

     &:active {
         color: #005077;
         transition: all 0.2s ease-in-out;
     }
 }

 .resetDashboard {
     color: black;
     background-color: white;
     margin: 12px 0;
     height: 60px;
     display: flex;
     flex-direction: row;
     justify-content: center;
     align-items: center;
     font-weight: bold;
     font-size: 15pt;
     border: none;
     border-radius: 12px;
     box-shadow: 0 1px 2px 1px rgba(0, 0, 0, .18);
     cursor: pointer;
     transition: all 0.2s ease-in-out;

     &:hover {
         translate: 0 -2px;
         box-shadow: 0 2px 3px 1px rgba(0, 0, 0, .24);
         transition: all .2s ease-in-out;
     }

     &:active {
         box-shadow: inset 0 2px 2px 1px rgba(0, 0, 0, .18);
         transition: all 0.2s ease-in-out;
     }

     p {
         font-size: 24px;
         margin: -3px 12px 0 0;
     }
 }