.tooltip {
    color: #005077;
    background-color: white;
    margin: 0;
    padding: 0 12px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: -30px;
    left: 30px;
    height: 33px;
    width: max-content;
    border-radius: 12px;
    box-shadow: 0 1px 6px 2px rgba(0, 0, 0, .21);
    pointer-events: none;
    opacity: 0;
    z-index: 1;
    transition: opacity 0.2s ease-in-out;
}